// import HMsearch from '../../components/HMsearch.vue'
// import xflSelect from '../../components/xfl-select/xfl-select.vue';
import uniIcons from "../../components/uni-icons/uni-icons.vue";
import uniPopup from "../../components/uni-popup/uni-popup.vue";
var res = global.isLogin();
var openid = uni.getStorageSync("openid");
console.log(openid);

export default {
  components: {
    // HMsearch,
    // xflSelect
    uniPopup,
    uniIcons
  },
  data() {
    return {
      gender: 0,
      openid: "",
      nickName: null,
      avatarUrl: null,
      isShow: true
    };
  },
  methods: {
    changeIndicatorDots(e) {
      this.indicatorDots = !this.indicatorDots;
    },
    changeAutoplay(e) {
      this.autoplay = !this.autoplay;
    },
    intervalChange(e) {
      this.interval = e.target.value;
    },
    durationChange(e) {
      this.duration = e.target.value;
    },
    // 留言
    goLiuyan() {
      uni.navigateTo({
        url: "/pages/liuyan/liuyan"
      });
    },
    // 建议
    goJianyi() {
      uni.navigateTo({
        url: "/pages/jianyi/jianyi"
      });
    },
    // 投诉
    goTousu() {
      uni.navigateTo({
        url: "/pages/toushu/toushu"
      });
    },
    gotoLiuyan() {
      uni.navigateTo({
        url: "/pages/liuyan/liuyan"
      });
    },
    gotoMy() {
      uni.navigateTo({
        url: "/pages/my/my"
      });
    },
    onTabItemTap: function(e) {
      // console.log(e);
    },
    open() {
      this.isShow = true;
    },
    close() {
      this.isShow = false;
    },

    // *******************************************************************************

    //登录
    login() {
      let _this = this;
      // uni.showLoading({
      // 	title: '登录中...'
      // });

      // 1.wx获取登录用户code
      uni.login({
        provider: "weixin",
        success: function(loginRes) {
          // console.log(loginRes)
          let code = loginRes.code;
          if (!_this.isCanUse) {
            //非第一次授权获取用户信息
            uni.getUserInfo({
              provider: "weixin",
              success: function(infoRes) {
                console.log(infoRes);
                console.log(_this);
                //获取用户信息后向调用信息更新方法
                _this.nickName = infoRes.userInfo.nickName; //昵称
                _this.gender = infoRes.userInfo.gender; //昵称
                _this.avatarUrl = infoRes.userInfo.avatarUrl; //头像
                _this.updateUserInfo(); //调用更新信息方法
              }
            });
          }

          //2.将用户登录code传递到后台置换用户SessionKey、OpenId等信息
          console.log("通过login接口的code换取openid");
          uni.request({
            // url: 'http://zm.dangjian.link/phone/rediectH5',
            url: "https://api.weixin.qq.com/sns/jscode2session",
            data: {
              js_code: code,
              grant_type: "authorization_code",
              appid: "wx2eeaf446720f498f",
              secret: "a5ae90e1149d4afaa07d5a8aacec93ef"
            },
            method: "GET",
            header: {
              "content-type": "application/json"
            },
            success: res => {
              console.info("登录成功返回的openId：" + res.data.openid);
              _this.openid = res.data.openid;
              //openId、或SessionKdy存储//隐藏loading
              uni.hideLoading();
            },
            fail: err => {
              console.info(error);
            }
          });
        }
      });
    },
    //向后台更新信息
    updateUserInfo() {
      let _this = this;
      uni.request({
        url: "http://zm.dangjian.link/phone/xiaochengxu/userMessage",
        data: {
          openid: _this.openid,
          sex: _this.gender,
          nickname: _this.nickName,
          headimgurl: _this.avatarUrl
        },
        method: "GET",
        header: {
          "content-type": "application/json"
        },
        success: res => {
          console.log(res);
          if (res.data.state == "success") {
            uni.reLaunch({
              //信息更新成功后跳转到小程序首页
              url: "/pages/index/index"
            });
          }
        }
      });
    }
  },

  onLoad(op) {
    uni.showModal({
      title: "平台介绍",
      content:
        "河南省机关事务局留言反馈投诉平台是解决人民问题最有效的投诉举报平台，河南省机关事务局留言反馈投诉平台是解决人民问题最有效的投诉举报平台。",
      success: function(res) {
        if (res.confirm) {
          console.log("用户点击确定");
        } else if (res.cancel) {
          console.log("用户点击取消");
        }
      }
    });
    console.log(op);
    // this.openid = op.openid
    // 把openid存入storage里 并在根组件中定义一个globe方法判断openID是否存在
    uni.setStorageSync("openid", op.openid);

    this.req.request(
      "/checkOpenid/?openid=" + op.openid,
      {},
      "GET",
      res => {
        // console.log(res)
        if (res.success) {
          uni.showToast({
            title: res.message,
            mask: true,
            duration: 1200,
            icon: "none"
          });
        }
      },
      function(e) {
        uni.showToast({
          title: e.errMsg,
          mask: true,
          duration: 1200,
          icon: "none"
        });
      }
    );

    this.login();
  }
};
